<style>
  .rightPanel{
    display: flex;
    display: -webkit-flex;
    -webkit-box-orient: vertical;
    -webkit-flex-flow: column;
    flex-flow: column;
  }
</style>
<template>
  <div class="right-container">
    <Row class="container-navigation">
      <iCol span="2">
      <img src="../../../assets/content/navigate_icon.png" height="20" width="20"/>
      </iCol>
      <iCol span="22">
      <span class="page-title"></span>
      </iCol>
    </Row>
    <!---->
    <div class="tool-panel">
      <Row  style="margin-top: 10px;">
        <Select class="grey" v-model="selectedBuildingId" clearable placeholder="" @on-change="searchDataByParam" style="width: 200px">
          <Option :value="building.buildingId" :key="building.buildingId" v-for="building in buildingList">{{ building.buildingName }}</Option>
        </Select>
        <Select class="grey" v-model="taskType" clearable placeholder="" @on-change="searchDataByParam" style="margin-left: 5px; width: 120px">
          <Option value="SUPERVISE" :key="'SUPERVISE'"></Option>
          <!--<Option value="REVIEW" :key="'REVIEW'"></Option>-->
          <Option value="CHECK" :key="'CHECK'"></Option>
        </Select>
        <iSelect class="grey" v-model="taskResult" placeholder="" clearable @on-change="searchDataByParam"
                 style="margin-left: 5px; width: 120px">
          <Option value="PASS" :key="'PASS'"></Option>
          <Option value="FAIL" :key="'FAIL'"></Option>
        </iSelect>
        <DatePicker type="daterange" v-model="dateRange" class="grey" placeholder="" style="margin-left: 5px; width: 180px"></DatePicker>
        <Button @click="searchDataByParam" class="grey" style="margin-left:5px;width:80px;"></Button>
      </Row>
    </div>
    <!---->
    <div class="body-table">
      <!---->
      <Table :columns="columns" border :data="taskList" class="auto-height-table grey" style="width:100%">
      </Table>
      <!---->
      <div style="margin-top: 10px;overflow: hidden">
        <div style="float: right;">
          <Page @on-change="searchData" :total="pageTotal" :pageSize="pageSize" class="default" :current="1"></Page>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import server from "../../../config/api";
  import common from '../../../libs/common';
  import util from '../../../libs/util';
  import Vue from 'vue';
  import niceScroll from 'jquery.nicescroll';

  export default {
    data(){
      return {
        newData: {
          name: undefined,
        },
        selectedBuildingId: undefined,
        taskType: undefined,
        taskResult: undefined,
        dateRange: [],
        taskList: [],
        common: undefined,
        searchBuildingId: undefined,
        pageTotal: undefined,
        buildingList:[],
        pageSize: 20,
        modal:false,
        searchParam:'',
        title:'',
        columns: [
          {
            title: '',
            align: 'center',
            type: 'index',
            width: '50'
          },
          {
            title: '',
            align: 'center',
            key: 'targetBuildingName'
          },
          {
            title: '',
            key: 'taskName',
            width: '180',
            align: 'center'
          },
          {
            title: '',
            align: 'center',
            width: '80',
            render: (h, params) => {
              let result = '';
              if(params.row.status === 'OPEN') {
                result = '';
              } else if(params.row.status === 'EXECUTING') {
                result = '';
              } else if(params.row.status === 'DONE') {
                if(params.row.result === 'PASS') {
                  result = '';
                } else if(params.row.result === 'FAIL') {
                  result = '';
                }
              }
              return h('span', result);
            }
          },
          {
            title: '',
            align: 'center',
            width: '120',
            render: (h, params) => {
              return h('span', this.common.revertMTime(params.row.planStartDt));
            }
          },
          {
            title: '',
            align: 'center',
            width: '100',
            render: (h, params) => {
              if(params.row.creatUser === undefined || params.row.creatUser === null) {
                return h('span', '-');
              }
              return h('span', params.row.creatUser.name);
            }
          },
          {
            title: '',
            align: 'center',
            render: (h, params) => {
              return h('span', params.row.createrOrgName);
            }
          },
          {
            title: '',
            align: 'center',
            width: '80',
            render: (h, params) => {
              return h('div', [
                h('i-button',{
                  props:{
                    type:'text',
                    size:'small'
                  },
                  style:{
                    color:'#a9b2fd'
                  },
                  on:{
                    click:()=>{
                      this.showDetail(params.row.taskId);
                    }
                  }
                },'')
              ]);
            }
          }
        ],
        formValidate: {
          name: [
            { required: true, message: ''}
          ]
        }
      };
    },
    methods: {
      initData: function() {
        // 
        this.searchData(1);
        // 
        this.searchBuildingList();
      },
      showDetail: function(taskId) {
        this.$router.push({name: 'recordDetail', query: {taskId: taskId}});
      },
      createData: function(formRef) {
        this.$refs[formRef].validate((valid) => {
          if (valid) {
            this.$Message.success('!');
          } else {
            this.$Message.error('!');
          }
        })
      },
      searchBuildingList: function() {
        // buildingList
        // 
        this.$http.get(server.api.organization.buildingList.list.replace('{orgId}', localStorage.current_org_id)).then((response) => {
          if (!response.body.meta.success) {
            this.$Message.error("");
            return;
          }
          this.buildingList = response.body.data;
        }, (error) => {
          this.$Message.error("");
        });
      },
      searchData:function (pageNum) {
        // 
        let url = server.api.routine.record.list.replace('{orgId}', localStorage.current_org_id) + '?orgId=' + localStorage.current_org_id + '&pageSize=' + this.pageSize + '&pageNum=' + pageNum;
        if(this.selectedBuildingId !== undefined) {
          url += '&buildingId=' + this.selectedBuildingId;
        }
        if(this.taskType !== undefined && this.taskType !== "") {
          url += '&taskType=' + this.taskType;
        } else {
          // 
          url += '&taskType=SUPERVISE,CHECK';
        }
        if(this.taskResult !== undefined && this.taskResult !== "") {
          url += '&taskResult=' + this.taskResult;
        }
        let range = [];
        if (this.dateRange !== undefined && this.dateRange !== null && this.dateRange.length !== 0
          && this.dateRange[0] !== null && this.dateRange[0].length !== 0) {
          range.push(util.dateFormat(this.dateRange[0], 'yyyy-MM-dd HH:mm:ss'));
          range.push(this.dateRange[1]);
          range[1].setDate(range[1].getDate() + 1);
          range[1].setMilliseconds(range[1].getMilliseconds() - 1);
          range[1] = util.dateFormat(range[1], 'yyyy-MM-dd HH:mm:ss');
          url += '&startDt=' + range[0];
          url += '&endDt=' + range[1];
        }
        this.$http.get(url).then((response) => {
          this.pageTotal = response.body.data.total;
          this.taskList = response.body.data.rows;
          Vue.nextTick(() => {
            $(".auto-height-table .ivu-table-body").niceScroll({
              cursorcolor: "rgb(99, 114, 131)",
              cursorwidth: "7px",
              cursoropacitymax: 0.4,
              cursorborder: "", // css definition for cursor border
              cursorborderradius: "",
              hidecursordelay: 1000,
            });
          });
        }, (error) => {
          this.$Message.error("");
        });
      },
      searchDataByParam: function() {
        // 
        this.searchData(1);
      }
    },
    mounted(){
      this.common = common;
      this.initData();
    },
    beforeRouteLeave(to, from, next){
      $(".auto-height-table .ivu-table-body").getNiceScroll().hide();
      next();
    }
  }
</script>



